<template>
  <div class="blog-detail">
    <b-container>
      <b-row>
        <b-col cols="12" md="5" sm="3">
          <img class="blog-detail__img " :src="entry.img" :alt="entry.imgAlt">
        </b-col>
        <b-col cols="12" md="7" sm="9">
          <div class="blog-detail__content">
            <h3 >{{entry.title}}</h3>
            <p class="blog-detail__text">
              {{entry.text}}
            </p>
          </div>
        </b-col>
      </b-row>
      <!-- <b-row>
        <b-col class="back-button">
          <router-link :to="{ name: 'Home' }" class="blog-detail__back-link"><b-button variant="outline-primary">Back</b-button></router-link>
        </b-col>
      </b-row> -->
    </b-container>
  </div>
</template>

<script>
import db from '../firebase'

export default {
  props: {
    index: {
      type: Number,
      required: true
    }
  },
  data () {
    return {
      entry: {}
    }
  },
  methods: {
    fetchEntry () {
      db.ref(`entries/${this.index}`).on('value', snap => {
        this.entry = snap.val()
      })
    }
  },
  watch: {
    '$route' (to, from) {
      this.index = to.params.index
      this.fetchEntry()
    }
  },
  created () {
    this.fetchEntry()
  }
}
</script>

<style>
  .back-button {
    text-align: right;
  }

  .blog-detail {
    position: relative;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background-color: white;
    padding: 2rem 0;
    z-index: 1;
  }

  .blog-detail__back-link {
    margin-top: 1rem;
    font-size: 18px;
  }

  .blog-detail__img {
    display: block;
    width: 100%;
    height: auto;
    margin-bottom: 20px;
  }

  .blog-detail__content > h3 {
    margin-top: 0;
    color: rgb(78, 94, 119);
  }
  .blog-detail__content > p {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  }

  .blog-detail__subtitle {
    color: #aaa;
  }
</style>



